<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">华为云文件直传</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                对象存储服务（Object Storage Service）是一款稳定、安全、高效、易用的云存储服务，具备标准Restful API接口，可存储任意数量和形式的非结构化数据！
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
              <p>通过policy方式，obsFileTransfer方法三个实参：</p>
              <p>1.files:文件对象</p>
              <p>2.getPolicyUrl:获取策略的地址</p>
              <p>3.fn:成功后的回调</p>
            </div>
            <div class="hb-code" style="display: block">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                              //华为云obs直传
                              obsFileTransfer(files, getPolicyUrl, fn) {
                                //参数说明
                                //*files Object 文件对象
                                //getPolicyUrl 获取policy，accessKeyId，host，signature地址，需后台提供
                                //fn function 上传后回调
                                if (!files) {
                                  return;
                                }
                                if (!KeyType) {
                                  return;
                                }
                                if (!getPolicyUrl) {
                                  return;
                                }
                                let filesObj = files.target.files[0];
                                let fileName = files.target.files[0].name;
                                let getPointLocal = fileName.indexOf(".");
                                let getHZ = fileName.slice(getPointLocal + 1);
                                let timeStamp = new Date().getTime();
                                let newFileName = `f${timeStamp}.${getHZ}`;
                                let keyVal = "filsObs/xxx"; //文件夹路径 根据具体业务

                                axios.get(getPolicyUrl).then((res) => {
                                    let d = res.data;
                                    let policy = d.data.policy;
                                    let accessKeyId = d.data.accessKeyId;
                                    let signature = d.data.signature;
                                    let hostUrl = d.data.host;
                                    let filePath = `${hostUrl}/${keyVal}`; //文件路径，回调时候返回
                                    if (d.code == "0") {
                                      let ffData = new FormData();
                                      ffData.append("key", keyVal);
                                      ffData.append("x-obs-acl", "public-read");
                                      ffData.append("content-type", "text/plain");
                                      ffData.append("policy", policy);
                                      ffData.append("AccessKeyId", accessKeyId);
                                      ffData.append("signature", signature);
                                      ffData.append("file", filesObj);
                                      axios.post(hostUrl, ffData, {
                                          headers: {
                                            Accept: "text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3",
                                            "Content-Type": "multipart/form-data"
                                          }
                                        })
                                        .then((res) => {
                                          fn(res, filePath);
                                        })
                                        .catch((err) => {
                                          fn(err, filePath);
                                        });
                                    }
                                  })
                                  .catch((err) => {});
                              }

                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>

        </div>

    </div>
</template>

<script>
    export default {
        name: 'wltxtlinkhref',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{

        }
    }
</script>
<style scoped>

</style>



